import React, { Component } from 'react';
import Zoom from 'react-reveal/Zoom';
import scrollToComponent from 'react-scroll-to-component';
import "react-toggle/style.css";
import Toggle from 'react-toggle';
import GoogleDocsViewer from 'react-google-docs-viewer';
import './App.css';
import ParticlesBackground from './ParticlesBackground';
import DarkParticlesTheme from './DarkParticlesTheme';
import Intro from './Intro';
import Technical from './TechnicalSkills';
import THKQ from './THKQCards';
import AirJordans from './AirJordans';
import Lululemonades from './Lululemonades';
import About from './About';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      dark: false,
      color: 'white',
      fontColor: 'black',
      resume: false
    };
  }

  onDarkThemeToggle() {
    console.log('you toggled');
    this.setState ({
      dark: true,
      color: 'black',
      fontColor: 'white'
    });
  }

  onLightThemeToggle() {
    this.setState ({
      dark: false,
      color: 'white',
      fontColor: 'black'
    });
  }
  
  onThemeClick() {
    if (!this.state.dark) {
      this.onDarkThemeToggle();
    } else {
      this.onLightThemeToggle();
    }
  }

  onResumeClick() {
    console.log('you clicked');
    this.setState ({
      resume: !this.state.resume
    });
  }

  render() {
    return (
      <div>
        {
          !this.state.dark ? <ParticlesBackground /> : <DarkParticlesTheme />
        }

        <div>
            
          <nav class="navbar" style={{position: 'fixed', right: '0px', color: `${this.state.fontColor}`}}>
            <div style={{textAlign: 'left', fontSize: '8pt'}}>
            <div class="navbar-brand cursor" onClick={() => scrollToComponent(this.Top, { offset: 0, align: 'bottom', duration: 450, ease:'inExpo'})}>TOP</div>
            <div class="navbar-brand cursor" onClick={() => scrollToComponent(this.About, { offset: -50, align: 'bottom', duration: 450, ease:'inExpo'})}>ABOUT</div>
            <div class="navbar-brand cursor" onClick={() => scrollToComponent(this.Portfolio, { offset: -70, align: 'bottom', duration: 450, ease:'inExpo'})}>PORTFOLIO</div>
            <div class="navbar-brand cursor" onClick={() => scrollToComponent(this.TechnicalSkills, { offset: -90, align: 'bottom', duration: 450, ease:'inExpo'})}>TECHNICAL SKILLS</div>
            <label><i class="far fa-sun fa-1x"></i></label>
              <Toggle
                className='custom-classname'
                defaultChecked={this.state.dark}
                icons={false}
                onChange={() => this.onThemeClick()} 
                />
            <label><i class="far fa-moon fa-1x"></i></label>
            </div>
          </nav>


          <div className="container">

            <div className="intro-page">
            <Zoom>
            <div className="intro" ref={(section) => { this.Top = section; }} style={{color: `${this.state.fontColor}`}}>
              <div className="inner">
        
              <Intro color={this.state.color} about={this.About}/>
                <div className="animated bounce arrow" style={{marginTop: '150px'}}>
                <div style={{fontSize: '15pt'}}>click here</div>
                  <i className="fas fa-angle-down fa-1x cursor" 
                  onClick={() => scrollToComponent(this.About, { offset: -50, align: 'bottom', duration: 450, ease:'inExpo'})}
                  />
                </div>
              </div>
            </div>
            </Zoom>
            </div>

            <div className="about" ref={(section) => { this.About = section; }} style={{color: `${this.state.fontColor}`}}>
              <div style={{fontSize: '26pt'}}>ABOUT
                <About color={this.state.color} />
                <div className="animated bounce" style={{marginTop: '100px'}}>
                  <div style={{fontSize: '15pt'}}>click here</div>
                    <i className="fas fa-angle-down fa-1x cursor" 
                    onClick={() => scrollToComponent(this.Portfolio, { offset: -70, align: 'bottom', duration: 450, ease:'inExpo'})}
                    />
                </div>
              </div>
            </div>

            <div className="portfolio" ref={(section) => { this.Portfolio = section; }} style={{color: `${this.state.fontColor}`}}>
              <div style={{fontSize: '26pt', textAlign: 'center'}}>
                PORTFOLIO
                <div className="d-flex flex-wrap justify-align-between">
                  <THKQ />
                  <AirJordans/>
                  <Lululemonades />
                </div>
                <div className="animated bounce" style={{marginTop: '100px'}}>
                  <div style={{fontSize: '15pt'}}>click here</div>
                    <i className="fas fa-angle-down fa-1x cursor"  
                    onClick={() => scrollToComponent(this.TechnicalSkills, { offset: -90, align: 'bottom', duration: 450, ease:'inExpo'})}
                    />
                </div>
              </div>
            </div>


            <div className="resume"
              ref={(section) => { this.TechnicalSkills = section; }} style={{color: `${this.state.fontColor}`, fontSize: '26pt'}}>
                TECHNICAL SKILLS
                <Technical color={this.state.color} />
                <div onClick={() => this.onResumeClick()} style={{fontSize: '26pt'}}>RESUME
                <span style={{marginLeft: '30px', fontSize: '10pt'}}>
                  <i className="far fa-file-alt fa-3x cursor" />
                  <div>click here for resume </div>
                </span>
                  {
                    this.state.resume && 
                    <GoogleDocsViewer
                      width="700px" 
                      height="680px" 
                      fileUrl="https://s3-us-west-1.amazonaws.com/kianna-portfolio/QuachKianna.pdf"
                    />
                  }
            </div>
                  <div className="animated bounce" style={{textAlign: 'center'}}>
                    <div style={{fontSize: '15pt'}}>click here</div>
                      <i class="fas fa-angle-up fa-1x cursor" 
                      onClick={() => scrollToComponent(this.Top, { offset: 0, align: 'bottom', duration: 450, ease:'inExpo'})}
                      />
                  </div>  
                </div>

              <div className="animated bounce top" style={{textAlign: 'center', color: `${this.state.fontColor}`}}>
                <div style={{fontSize: '15pt'}}>top</div>
                <i class="far fa-hand-point-up fa-3x cursor"
                  onClick={() => scrollToComponent(this.Top, { offset: 0, align: 'bottom', duration: 450, ease:'inExpo'})}
                />
                <div style={{fontFamily: 'lato', fontSize: '15pt'}}>
                  <a target="_blank" rel="noopener noreferrer" style={{textDecoration: 'none'}} href="https://github.com/kiannaquach" className="icons">Kianna🐥</a>
                </div>
              </div>       
          </div>
        </div>
      </div>
    );
  }
}

export default App;



